$background-color: #f1faee;
$primary-color: #1d3557;
$secondary-color: #457b9d;
$tertiary-color: #80d8da;
$other-color: #e63946;

* {
    box-sizing: border-box;
    outline: none;
    text-decoration: none;
    color: #edf6f9;
    margin: 0;
    padding: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 1rem;
    scroll-behavior: smooth;

    &::-webkit-scrollbar {
        display: none;
    }
}

body {
    background: black;
    overflow-x: hidden;
    // overflow: hidden;

    .navigation-container {
        background: rgba(white, .15);
        backdrop-filter: blur(.2rem);
        position: absolute;
        top: 5px;
        left: 50%;
        translate: -50% 0;
        width: calc(100% - 10px);
        height: 4.5rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: .75rem;
        justify-content: space-evenly;
        border-radius: .3rem;
        font-size: .9rem;
        font-weight: 500;
        z-index: 4;

        .logo-container {
            width: 10rem;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            cursor: pointer;

            .ico {
                width: 2rem;
                height: 2rem;
                border-radius: 58% 42% 58% 42% / 50% 49% 51% 50%;
                background: $tertiary-color;
                margin-right: .75rem;
            }

            .logo {
                height: 100%;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                line-height: 2rem;

                .text {
                    font-weight: 700;
                    font-size: 2rem;
                    cursor: pointer;
                    line-height: 1.5rem;
                }
            }
        }

        .navigation-wrapper {
            width: calc(90% - 10rem);
            height: 100%;
            // background: teal;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            .navigation {
                display: flex;
                align-items: center;
                justify-content: space-evenly;
                // background: goldenrod;
                padding: 0 .8rem;
                width: 60%;
                height: 100%;

                a {
                    color: white;
                    position: relative;
                    text-transform: uppercase;
                    font-size: .95rem;

                    &::after {
                        content: '';
                        position: absolute;
                        height: 2px;
                        width: 0;
                        left: 50%;
                        translate: -50% 0;
                        bottom: -2px;
                        background: white;
                        transition: .3s ease;
                    }

                    &:hover::after {
                        width: 100%;
                    }
                }
            }
        }
    }

    .home-wrapper {
        height: 100vh;
        position: relative;

        .home-container {
            width: 100vw;
            height: 100vh;
            // background: orange;
            position: relative;
            scroll-snap-align: start;

            .home-navigation {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 5;
                width: 100%;
                height: 4rem;
                display: flex;
                flex-direction: row;
                align-items: center;
                padding: .75rem;
                justify-content: space-evenly;
                font-size: .95rem;
                font-weight: 500;
                z-index: 4;

                .logo-container {
                    width: 10rem;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: white;
                    cursor: pointer;

                    .ico {
                        width: 2rem;
                        height: 2rem;
                        border-radius: 58% 42% 58% 42% / 50% 49% 51% 50%;
                        background: $tertiary-color;
                        margin-right: .75rem;
                    }

                    .logo {
                        height: 100%;
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .text {
                            font-weight: 700;
                            font-size: 2rem;
                            cursor: pointer;
                        }
                    }
                }

                .burger {
                    display: none;
                }

                .navigation-wrapper {
                    width: calc(90% - 10rem);
                    height: 100%;
                    // background: teal;
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;

                    .close {
                        display: none;
                    }

                    .navigation {
                        display: flex;
                        align-items: center;
                        justify-content: space-evenly;
                        // background: goldenrod;
                        padding: 0 .8rem;
                        width: 60%;
                        height: 100%;

                        a {
                            color: white;
                            position: relative;
                            text-transform: uppercase;
                            font-size: .95rem;

                            &::after {
                                content: '';
                                position: absolute;
                                height: 2px;
                                width: 0;
                                left: 50%;
                                translate: -50% 0;
                                bottom: -2px;
                                background: white;
                                transition: .3s ease;
                            }

                            &:hover::after {
                                width: 100%;
                            }
                        }
                    }
                }
            }

            .wrapper {
                width: 100%;
                height: 100%;
                // position: absolute;
                // top: 0;
                // left: 0;
                z-index: -1;

                display: grid;
                grid-template-columns: repeat(var(--columns), 1fr);
                grid-template-rows: repeat(var(--rows), 1fr);

                .tile {
                    background: black;
                    outline: 1px solid white;
                }
            }

            .content {
                width: 100%;
                height: calc(100% + 2px);
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                display: flex;
                align-items: center;
                justify-content: center;
                background: rgba(black, .5);
                backdrop-filter: blur(.05rem);
                color: $secondary-color;
                line-height: 100%;
                font-weight: 600;
                padding: 0 5rem;

                .left {
                    position: relative;
                    line-height: 100%;
                    font-size: 3rem;
                    width: 50%;
                    height: 100%;
                    // border: 1px solid #ff00ff;

                    .text {
                        line-height: 3rem;
                        position: absolute;
                        top: 80%;
                        left: 40%;
                        translate: -50% 50%;
                        color: white;
                        // border: 1px solid black;
                        transform-origin: 0 0;
                        transform: rotate(-90deg);
                        font-size: 2.5rem;
                    }
                }

                .right {
                    line-height: 100%;
                    width: 50%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-evenly;
                    padding: 8rem 0;

                    span {
                        line-height: 100%;
                        color: white;
                        font-size: 8rem;
                        font-weight: 800;

                        span {
                            color: $tertiary-color;
                        }
                    }
                }
            }
        }

        // .hover {
        //     position: absolute;
        //     width: 20rem;
        //     height: 20rem;
        //     scale: 1 1.15;
        //     background: linear-gradient(to right, white, grey, gray, black);
        //     border-radius: 55% 45% 50% 50% / 41% 48% 52% 59%;
        //     z-index: 5;
        //     translate: -50% -50%;
        //     animation: rotate 10s linear infinite;
        //     filter: blur(5rem) opacity(.8);
        //     z-index: -2;
        //     transition: .5s ease-out;
        // }

        .home-content-wrapper {
            // height: 200vh;
            position: relative;
            overflow: hidden;
        }
    }

    .contact-container {
        margin-top: calc(5rem + 5px);
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 3rem 5rem;

        .contact {
            width: 100%;
            // background: white;
            // aspect-ratio: 16 / 7;

            .content {
                font-size: 0.85em;
                width: 100%;
                // height: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 1.5rem;
                // background: url("../../public/images/grayBackground.svg");
                background-size: 6px;
                // border: .3rem solid black;
                background: rgba(white, .5);
                backdrop-filter: blur(5rem);
                border-radius: .25rem;

                .left {
                    width: 36%;
                    // border: 1px solid $secondary_color;
                    border: 0.3rem solid black;
                    display: flex;
                    position: relative;
                    flex-direction: column;
                    align-items: center;
                    background: white;
                    box-shadow: 0.5rem 0.5rem 0 0 #000;

                    .title_cont {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        width: 100%;
                        height: 2.5rem;
                        text-transform: uppercase;
                        // margin: 0.75rem 0 0.65rem 0;
                        border-bottom: 0.3rem solid black;
                        background: #c4c4c4;

                        .utils {
                            width: 4rem;
                            height: 100%;
                            // background: orange;
                            display: flex;
                            align-items: center;
                            justify-content: space-evenly;

                            // flex-direction: ;
                            .dot {
                                height: 50%;
                                aspect-ratio: 1;
                                border: 0.3rem solid black;
                                background: white;
                                border-radius: 50%;

                                &:nth-child(1) {
                                    background: gray;
                                }

                                &:nth-child(2) {
                                    background: lightgray;
                                }
                            }
                        }

                        .title {
                            font-size: 1.2rem;
                            font-weight: 700;
                            color: black;
                            translate: calc(-100% - 2rem);
                        }
                    }

                    .container {
                        padding: 1rem;
                        width: 100%;

                        .cont {
                            font-size: 0.85em;
                            // margin: .5em 0;
                            display: flex;
                            align-items: center;
                            width: 100%;
                            display: flex;
                            align-items: center;

                            a,
                            button {
                                // border: 1px solid $secondary_color;
                                // border-radius: 0.2em;
                                // color: white;
                                border: 0.3rem solid black;
                                background-color: #c4c4c4;
                                display: flex;
                                align-items: center;
                                margin: 0.25em 0;
                                user-select: none;
                                padding: 1.6em 1.5em;
                                width: 100%;
                                font-size: 1.25em;
                                font-weight: 600;
                                cursor: pointer;
                                color: black;

                                svg {
                                    margin-right: 1.25em;
                                    fill: #000000;
                                }

                                &:hover {
                                    background: black;
                                    color: white;

                                    // color: white;
                                    svg {
                                        fill: white;
                                    }
                                }
                            }
                        }
                    }
                }

                .right {
                    width: 60%;
                    // padding: 0.5em 1.25em;
                    // background-color: $accent_color;
                    background: white;
                    border: 0.3rem solid black;
                    position: relative;
                    box-shadow: 0.5rem 0.5rem 0 0 #000;

                    .title_cont {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        width: 100%;
                        height: 2.5rem;
                        text-transform: uppercase;
                        // margin: 0.75rem 0 0.65rem 0;
                        border-bottom: 0.3rem solid black;
                        background: #c4c4c4;

                        .utils {
                            width: 4rem;
                            height: 100%;
                            // background: orange;
                            display: flex;
                            align-items: center;
                            justify-content: space-evenly;

                            // flex-direction: ;
                            .dot {
                                height: 50%;
                                aspect-ratio: 1;
                                border: 0.3rem solid black;
                                background: white;
                                border-radius: 50%;

                                &:nth-child(1) {
                                    background: gray;
                                }

                                &:nth-child(2) {
                                    background: lightgray;
                                }
                            }
                        }

                        .title {
                            font-size: 1.1rem;
                            font-weight: 700;
                            text-transform: uppercase;
                            margin-bottom: 0;
                            color: black;
                            translate: calc(-100% - 2rem);

                            &::after {
                                width: 0;
                            }
                        }
                    }

                    .form {
                        padding: 1rem;
                        width: 100%;
                        display: grid;
                        grid-template-columns: 1fr 1fr;
                        column-gap: 0.5rem;
                        row-gap: 0.5rem;

                        .input {
                            // margin: 1em 0;

                            .cont {
                                // border-radius: 0.2rem;
                                // border: 2px solid transparent;
                                // transition: all 0.2s ease;
                                // border: 1px solid $secondary_color;
                                background: #c4c4c4;
                                width: 100%;
                                // height: 4em;
                                aspect-ratio: 5.3 / 1;
                                display: flex;
                                border: 0.3rem solid black;
                                // margin: 0 0 .5rem 0;

                                label {
                                    width: 15%;
                                    height: 100%;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;

                                    svg {
                                        width: 100%;
                                        fill: black;
                                        // transition: all 0.2s ease;
                                    }
                                }

                                input {
                                    width: 85%;
                                    height: 100%;
                                    outline: none;
                                    border: none;
                                    font-weight: 600;
                                    background: none;
                                    color: black;

                                    &::-webkit-input-placeholder {
                                        color: black;
                                        text-transform: capitalize;
                                        user-select: none;
                                    }

                                    &:-webkit-autofill,
                                    &:-webkit-autofill:hover,
                                    &:-webkit-autofill:focus,
                                    &:-webkit-autofill:active {
                                        -webkit-text-fill-color: #000000;
                                        transition: background-color 999999999999s ease-in-out 0s;
                                    }

                                    &:-webkit-autofill:focus {
                                        -webkit-text-fill-color: #ffffff;
                                    }
                                }

                                &:focus-within {
                                    background: black;

                                    label {
                                        svg {
                                            fill: white;
                                        }
                                    }

                                    input {
                                        color: white;

                                        &::-webkit-input-placeholder {
                                            color: white;
                                        }
                                    }
                                }

                                .msg {
                                    min-height: 10em;
                                    max-height: 50em;
                                    max-width: 90%;
                                    min-width: 90%;
                                }
                            }

                            .req {
                                display: none;
                                color: #e70303;
                                font-size: 0.85em;
                                padding: 0 0 0 0.5em;
                            }
                        }

                        .txt_cont {
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            width: 100%;
                            grid-column: 1 / 3;

                            textarea {
                                min-height: 12em;
                                max-height: 40em;
                                min-width: 100%;
                                max-width: 100%;
                                background-color: #c4c4c4;
                                // border: 1px solid $secondary_color;
                                border: 0.3rem solid black;
                                // border-radius: 0.2rem;
                                padding: 1em 1.2em;
                                // transition: 0.2s ease;
                                outline: none;
                                resize: none;
                                font-weight: 600;
                                color: black;

                                &::-webkit-input-placeholder {
                                    color: #000000;
                                    text-transform: capitalize;
                                    user-select: none;
                                    font-weight: 600;
                                }

                                &:focus {
                                    // border-radius: 2em;
                                    background: black;
                                    color: white;

                                    &::-webkit-input-placeholder {
                                        color: white;
                                    }
                                }
                            }

                            .req {
                                display: none;
                                color: #e70303;
                                font-size: 0.85em;
                                margin: 0.25em 0 0 0;
                                padding: 0 0 0 0.5em;
                            }
                        }

                        input[type="button"] {
                            background: #c4c4c4;
                            height: 4rem;
                            font-size: 0.9rem;
                            letter-spacing: 0.01rem;
                            font-weight: 500;
                            aspect-ratio: 7 / 2.5;
                            user-select: none;
                            cursor: pointer;
                            color: black;
                            border: 0.3rem solid black;

                            &:hover {
                                background: black;
                                color: white;
                            }
                        }
                    }
                }
            }
        }
    }

    .resume-container {
        margin-top: calc(5rem + 5px);
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 3rem 5rem;

        .resume {
            // background: orange;
            width: 100%;

            .download-container {
                width: 100%;
                margin-bottom: 5rem;

                .download {
                    width: 100%;

                    .title {
                        font-size: 4rem;
                        font-weight: 800;
                        line-height: 4rem;
                        margin-bottom: 2rem;
                    }

                    .content {
                        font-size: 1.2rem;
                        font-weight: 200;
                        margin-top: .5rem;
                        line-height: 1.2rem;

                        a {
                            position: relative;

                            &::after {
                                content: '';
                                position: absolute;
                                left: 50%;
                                bottom: 0;
                                width: 100%;
                                height: 2px;
                                translate: -50% 0;
                                background: $tertiary-color;
                            }
                        }
                    }
                }
            }

            .education-container {
                width: 100%;
                position: relative;

                .education {
                    width: 100%;
                    padding-top: 3rem;
                    // margin: 0 5rem;
                    display: flex;
                    // justify-content: flex-start;
                    align-items: flex-start;

                    .title {
                        font-weight: 700;
                        font-size: 2rem;
                        // line-height: 1rem;
                        width: 50%;
                        // border: 1px solid magenta;
                    }

                    .content {
                        width: 50%;
                        display: flex;
                        flex-direction: column;
                        gap: 2rem;


                        .item {
                            display: flex;
                            align-items: flex-start;
                            justify-content: space-evenly;

                            .left {
                                // border: 1px solid magenta;
                                width: 15%;
                                height: 100%;

                                .date {
                                    font-weight: 200;
                                    font-size: .95rem;
                                }
                            }

                            .right {
                                // border: 1px solid magenta;
                                width: 70%;
                                height: 100%;
                                display: flex;
                                flex-direction: column;
                                align-items: flex-start;
                                justify-content: space-between;
                                gap: 1rem;
                                // justify-content: flex-start;

                                .top {
                                    width: 100%;

                                    .title {
                                        font-weight: 500;
                                        font-size: 1rem;
                                        line-height: 1rem;
                                    }
                                }

                                .bottom {
                                    width: 100%;

                                    .content {
                                        width: 70%;
                                        font-weight: 200;
                                        font-size: .95rem;
                                        line-height: 1rem;
                                    }
                                }
                            }
                        }
                    }
                }

                &::before {
                    content: '';
                    width: 100%;
                    position: absolute;
                    height: .5px;
                    top: 0;
                    left: 50%;
                    translate: -50% 0;
                    background: white;
                }
            }


        }
    }

    .about-container {
        margin-top: calc(5rem + 5px);

        .about-wrapper {
            height: 100%;
            width: 100%;

            .about-content {
                width: 100%;
                height: 100%;
                display: flex;
                padding: 4rem 1rem;

                .left {
                    width: 50%;
                    height: 100%;
                    padding: 1rem;
                    // border: 1px solid teal;

                    .title {
                        // border: 1px solid magenta;
                        font-size: 6rem;
                        line-height: 6rem;
                        font-weight: 800;

                        span {
                            margin: 0 0 0 3rem;
                        }
                    }

                    .content {
                        // border: 1px solid green;
                        height: calc(100% - 12rem);
                        font-size: .95rem;
                        font-weight: 600;
                        padding: 1rem;
                        padding-top: 2.5rem;

                        .technologies {
                            width: 100%;
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            justify-content: center;
                            .title {
                                width: 100%;
                                text-align: center;
                                font-size: 2rem;
                                font-weight: 700;
                            }
                            .container {
                                width: 95%;
                                .swiper-container {
                                    display: flex;
                                    .swiper-slide {
                                        aspect-ratio: 1;
                                        width: 1rem;
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                        img {
                                            width: 100%;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                .right {
                    width: 50%;
                    height: 100%;
                    padding: 1rem;

                    .image-container {
                        width: 100%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .image {
                            width: 70%;
                            aspect-ratio: 1;
                            // background: rgb(129, 51, 51);
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border: 3px solid white;

                            img {
                                width: calc(100% - 12px);
                                height: calc(100% - 12px);
                                aspect-ratio: 1;
                                filter: grayscale(80%);
                                position: relative;
                                user-select: none;
                                pointer-events: none;
                            }
                        }

                    }
                }

            }
        }
    }

    .outreach-container {
        margin-top: calc(5rem + 5px);
        padding: 3rem 5rem;

        .outreach {
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 4rem;

            .title-container {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 2rem;

                .title {
                    font-size: 4rem;
                    font-weight: 800;
                    line-height: 4rem;
                }

                .sub {
                    font-size: 1.1rem;
                    font-weight: 600;
                    letter-spacing: .05rem;
                }
            }

            .content {
                padding-top: 4rem;
                position: relative;
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 2rem;

                .item {
                    width: 80%;
                    display: flex;
                    justify-content: space-between;

                    .text {
                        width: calc(50% - 2rem);
                        display: flex;
                        flex-direction: column;
                        // justify-content: space-between;
                        gap: .8rem;

                        .title {
                            font-size: 2rem;
                            font-weight: 600;
                            line-height: 2rem;
                            letter-spacing: .05rem;
                            text-transform: capitalize;
                        }

                        .sub {
                            font-size: .95rem;
                            font-weight: 300;
                            letter-spacing: .025rem;
                            line-height: 1.5rem;
                        }
                    }

                    .image-container {
                        width: 50%;
                        height: 100%;
                        position: relative;

                        // aspect-ratio: 1;
                        // border: 1px solid white;
                        .image {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border: 3px solid white;
                            background: black;
                            aspect-ratio: 1;

                            img {
                                width: calc(100% - 12px);
                                height: calc(100% - 12px);
                                // aspect-ratio: 1;
                                filter: grayscale(80%);
                                position: relative;
                                user-select: none;
                                pointer-events: none;
                                object-fit: cover;
                            }
                        }
                    }
                }

                &::before {
                    content: '';
                    width: 100%;
                    position: absolute;
                    height: .5px;
                    top: 0;
                    left: 50%;
                    translate: -50% 0;
                    background: white;
                }
            }
        }
    }

    .personal-container {
        margin-top: calc(5rem + 5px);
        padding: 3rem 5rem;

        .personal {
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 4rem;

            .title-container {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 2rem;

                .title {
