        .personal-container {
            margin-top: calc(5rem + 5px);
            padding: 3rem 5rem;

            .personal {
                width: 100%;
                display: flex;
                flex-direction: column;
                gap: 4rem;

                .title-container {
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 2rem;

                    .title {
                        font-size: 4rem;
                        font-weight: 800;
                        line-height: 4rem;
                    }

                    .sub {
                        font-size: 1.1rem;
                        font-weight: 600;
                        letter-spacing: .05rem;
                    }
                }

                .content {
                    padding-top: 4rem;
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 2rem;

                    .item {
                        width: 80%;
                        display: flex;
                        justify-content: space-between;

                        .text {
                            width: calc(50% - 2rem);
                            display: flex;
                            flex-direction: column;
                            // justify-content: space-between;
                            gap: .8rem;

                            .title {
                                font-size: 2rem;
                                font-weight: 600;
                                line-height: 2rem;
                                letter-spacing: .05rem;
                                text-transform: capitalize;
                            }

                            .sub {
                                font-size: .95rem;
                                font-weight: 300;
                                letter-spacing: .025rem;
                                line-height: 1.5rem;
                            }
                        }

                        .image-container {
                            width: 50%;
                            height: 100%;
                            position: relative;

                            // aspect-ratio: 1;
                            // border: 1px solid white;
                            .image {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                border: 3px solid white;
                                background: black;
                                aspect-ratio: 1;

                                img {
                                    width: calc(100% - 12px);
                                    height: calc(100% - 12px);
                                    // aspect-ratio: 1;
                                    filter: grayscale(80%);
                                    position: relative;
                                    user-select: none;
                                    pointer-events: none;
                                }
                            }
                        }
                    }

                    &::before {
                        content: '';
                        width: 100%;
                        position: absolute;
                        height: .5px;
                        top: 0;
                        left: 50%;
                        translate: -50% 0;
                        background: white;
                    }
                }
            }
        }

        .footer {
            height: 20vh;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            &::before {
                content: '';
                width: calc(90% - 1rem);
                position: absolute;
                height: .5px;
                top: 0;
                left: 50%;
                translate: -50% 0;
                background: white;
            }

            .footer-wrapper {
                height: 100%;
                width: 100%;
                display: grid;
                grid-template-columns: auto auto auto auto;
                // padding: 0 2rem;
                align-items: center;
                justify-content: space-evenly;

                .links {
                    width: 100%;

                    .title {
                        font-weight: 800;
                        letter-spacing: .05rem;
                        margin-bottom: .25rem;
                    }

                    .content {
                        font-weight: 100;
                        text-decoration: underline;
                        font-size: .975rem;
                    }
                }
            }
        }
    }
}

@keyframes hover {
    0% {
        background: black;
    }

    50% {
        background: whitesmoke;
    }

    100% {
        background: black;
    }
}